import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Icon } from '@iconify/react';

import { IMPORTATNT_TICKET_SELL } from '../../_mocks';

import { API_TICKET } from '../../config';

import useLocales from '../../hooks/useLocales';

import axios from '../../utils/axios';
import { fDateTime } from '../../utils/formatTime';
import { fNumber } from '../../utils/formatNumber';

import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketAds({ id }) {
    const { t } = useLocales();
    const [ticket, setTicket] = useState(null);

    useEffect(() => {
        axios.get(`${API_TICKET.getTicketDetail}/${id}`).then((res) => {
            setTicket(res.data.data.ticket);
        });
    }, [id]);

    if (!ticket) return <div />;

    return (
        <div className="flex flex-col mb-8 justify-center items-center">
            <div className="flex flex-col gap-8 w-full">
                <h3 className="text-[64px]">{t('title.ticket_ads_finalize')}</h3>

                <GradientBorderWrapper>
                    <div className="w-full justify-between flex flex-col md:flex-row px-6 py-[34px]">
                        <h3 className="text-[28px] font-medium">{ticket.note}</h3>
                        <button className="btn btn-primary px-8 py-4 h-auto text-[19px] font-normal capitalize rounded-2xl">{t('action.ads_manage')}</button>
                    </div>
                </GradientBorderWrapper>

                <GradientBorderWrapper>
                    {/* what */}
                    <div className="flex flex-col p-6">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_ads_what')}</h5>
                            <p className="text-stone-400">
                                {ticket.seat.length} ingresso para {ticket.note}
                            </p>
                        </div>
                    </div>
                    <hr className={`border-t-1 border-stone-600 my-2 mx-6`} />
                    {/* price */}
                    <div className="flex flex-col p-6">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_ads_price')}</h5>
                            <p className="text-stone-400">R${fNumber(ticket.originPrice)} por ingresso</p>
                        </div>
                            <Link className="text-sm text-primary underline mt-4">{t("action.edit_price")}</Link>
                    </div>
                    <hr className={`border-t-1 border-stone-600 my-2 mx-6`} />
                    {/* ticket event */}
                    <div className="flex flex-col p-6">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_ads_event_date')}</h5>
                            <p className="text-stone-400">
                                {ticket.event.name}, {fDateTime(ticket.event.dateTime)}
                            </p>
                        </div>
                    </div>
                    <hr className={`border-t-1 border-stone-600 my-2 mx-6`} />
                    {/* ticket location */}
                    <div className="flex flex-col p-6">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_ads_location')}</h5>
                            <p className="text-stone-400">{ticket.event.location}</p>
                        </div>
                    </div>
                    <hr className={`border-t-1 border-stone-600 my-2 mx-6`} />
                    {/* ticket transfer */}
                    <div className="flex gap-2 justify-between p-6 items-center">
                        <div className="flex flex-col gap-1">
                            <h5 className="text-[28px] font-medium">{t('title.ticket_ads_transfer')}</h5>
                            <p className="text-stone-400">341 - 9649 - **** 4355</p>
                        </div>
                    </div>
                </GradientBorderWrapper>
            </div>
            <div className="h-8" />
            <div className="flex flex-col gap-8 w-full">
                <GradientBorderWrapper>
                    <div className="p-6 w-full gap-4 flex flex-col">
                        <h5 className="text-[28px] font-medium mb-1">{t("title.important_information")}</h5>

                        {IMPORTATNT_TICKET_SELL.map((important, index) => (
                            <div className="flex gap-6 justify-start" key={index}>
                                <div className="bg-secondary text-primary rounded-full flex-1 px-1 h-6 w-6 flex justify-center items-center">
                                    <Icon icon="ic:round-arrow-forward" className="text-primary" />
                                </div>
                                <p className="text-stone-400">{important.content}</p>
                            </div>
                        ))}
                    </div>
                </GradientBorderWrapper>
            </div>
        </div>
    );
}
